import { useContext, useState } from "react";
import { DataContext } from "../../context/DataProvider";
import { Box, MenuItem, Menu,Typography } from "@mui/material";
import styled from "styled-components";
import { PowerSettingsNewOutlined } from "@mui/icons-material";



const Component = styled(Menu)`
margin-top:.4rem;`


export const Profile = () =>{
    const [{account,setAccount}] = useContext(DataContext);
    const [open,setOpen] =useState(false)
 



    const handleClick = (event)=>{
        setOpen(event.currentTarget);
    }

    const logOut =()=>{
        setAccount('');
    }

    function handleClose(){
setOpen(false);
    }
    console.log(account,'my name');
    return(
        <>

<Box>
    <Typography style={{marginLeft:"3rem", cursor:"pointer",marginBottom:'1rem'}} onClick={handleClick}>
        {account}
    </Typography>
    
</Box>
<Component
       
        anchorEl={open}
        open={Boolean(open)}
        onClose={handleClose}
       
      >
    
        <MenuItem onClick={() => {handleClose();logOut()}}>
        
      <PowerSettingsNewOutlined  color="primary"/>
      <Typography style={{fontSize:"1rem" , marginLeft:"1rem"}}>Logout</Typography>
        </MenuItem>

          </Component>
</>
    )
}

export default  Profile;