import React, { createContext, useState } from "react"; // Import useState and React

export const DataContext = createContext(null);

function DataProvider({children}){

 const [account, setAccount] = useState('');
 const [oldCart,setOldCart] =useState('');
 const[login,setLogined] =useState('no')
  return (
    
    <DataContext.Provider value={[{ account, setAccount},{oldCart,setOldCart},[login,setLogined] ]}>
    {children}
    </DataContext.Provider>

    
  );
};

export default DataProvider; // Export the component with the correct naming convention

