
import { Box, Button, Typography } from "@mui/material"
import styled from "styled-components"
import { ShoppingCart } from "@mui/icons-material"
import { FlashOn } from "@mui/icons-material"
import {  addtoCart, fetchProductDetails } from "../../redux/reducer/Slice2"
import { useDispatch } from "react-redux"
import {useNavigate, useParams}  from 'react-router-dom'
import { NavLink } from "react-router-dom"
import { paytmPay } from "../Login/api"

const Left = styled(Box)`
min-width :43%;
padding: 2.5rem 0 0 5rem;
display:flex:

`

const StyledButton = styled(Button)`
width:46%;
height:2.8rem;
border-radius:.1rem;
`

const Image = styled('img')`
padding:1rem;
width:95%;

 
`

 const DetailAction =({product})=>{


   const {id} = useParams()
   console.log(id)
   const dispatch =useDispatch()
const handleClick=()=>{

   dispatch(addtoCart(product))
   
}



return(

<Left key={product.id}>
<Box style={{padding:'-1rem 1rem', width:"90%",marginRight:"1rem"}}>
<Image  src={product.images[1]} alt="auto" />
</Box>
<NavLink to={'/cart'}>
<StyledButton variant="cointained"  style={{background: '#ff9f00',marginRight:".8rem",fontSize:".7rem", color:"white"}} onClick={handleClick}> <ShoppingCart />Add To Cart</StyledButton>
</NavLink>
<NavLink to={"/paytm"}><StyledButton variant="cointained" color="primary" style={{background:"#fb541b", color:"white"}} ><FlashOn />Buy Now  </StyledButton> </NavLink>



</Left>

)

}


export default DetailAction;