import { Box } from "@mui/material";
import SmartphoneSlide from "../Slides/SmartPhone";
import Midsection from "./midsection";
const Slides =()=>{



    return(
        <Box>
    
  
<SmartphoneSlide title={"HIgh Performance SmartPhones"} timer={true}  cate={'smartphones'} />
<Midsection />
<SmartphoneSlide title={"Newly Launched Laptops"} timer={false}  cate={'laptops'} />

<SmartphoneSlide title={"Fragnaces"} timer={false}  cate={'fragrances'} />

<SmartphoneSlide title={"Natural Care Skincare"} timer={false}  cate={'skincare'} />
<Midsection />
<SmartphoneSlide title={"Daily Needs for Home"} timer={false}  cate={'groceries'} />
<SmartphoneSlide title={"Design Your Heavens Home Decoration"} timer={false}  cate={'home-decoration'} />

        </Box>
    )
}

export default Slides;