import { Button,Box, Typography, Badge } from "@mui/material";
import styled from "styled-components";
import {  Circle, ShoppingCart } from "@mui/icons-material";
import Profile from "./profile";

import LoginDialog from "../Login/LoginDialog";
import { useState,useContext } from "react";
import  { DataContext } from "../../context/DataProvider";
import { NavLink } from "react-router-dom";
import { useSelector } from "react-redux";
const Wrapper= styled(Box)`
display:flex;
flex-direction:row;
gap:3rem;
font-size:1.2rem;
margin-top:.7rem;

@media (max-width: 426px) {
    /* CSS changes for screens with a maximum width of 768px */
    flex-direction: column;
    gap: 0.5remrem;
    font-size: 1rem;
    margin-top: 0.5rem;
   
  }

`
const Wrappers= styled(Box)`
display:flex;
gap:auto;


` 
const CustBadge = styled(Badge)`
  @media (max-width: 426px) {
    .MuiBadge-badge {
   display:none;

    }
  };
`


function CustomButton(){
    const [open,setOpen] =useState(false);
    const [{account}] = useContext(DataContext);

    const cart = useSelector((state)=>state.cart.cart)
      
    const totalQuantity = cart.reduce((total, item) => total + item.quantity, 0);
    console.log(totalQuantity,"its in header")
 const setDialog =()=>{
    setOpen(true);
    console.log("name at dialog is ",account);


   
 }

    return(
      <Wrapper>
      {account ? (<Profile  />):

     ( <Button style={{background:"white",width:"4.7rem",height:"2rem", marginLeft:".8rem",borderRadius:"0",fontWeight:"600",marginBottom:'1rem'}} varient="cointained"  onClick={ ()=>setDialog()}>LOGIN</Button>
      )}
           <Typography >Become a Seller</Typography>
           <Typography>More</Typography>
        <Wrappers>
          <NavLink to={'/cart'} style={{color:'inherit',display:'flex',textDecoration:'none'}}>
         <CustBadge  color='secondary' badgeContent={totalQuantity} >
          <ShoppingCart />
          </CustBadge>      
            <Typography style={{marginLeft:'.5rem'}}>Cart</Typography>
            </NavLink> 
        </Wrappers>
        <LoginDialog 
            open={open}
            setOpen={setOpen}
        />

    </Wrapper>

        
        
    )
}


export default CustomButton;


