import { Box } from "@mui/material";
import imageURL from "../../constants/data";

const Midsection = ()=>{

    return(
<Box style={{display:"flex"}}>
{imageURL.map((image)=>(
<Box key={image}>
<img  style={{width:"100%"}} src={image} alt="auto"/>
</Box>
))}

</Box>

    )
}

export default Midsection;