import { Box, InputBase, ListItem,List, Typography } from "@mui/material";
import styled from "styled-components";
import SearchIcon from '@mui/icons-material/Search';
import { useState } from "react";
import { useSelector } from "react-redux";
import { nanoid } from "@reduxjs/toolkit";
import { NavLink } from "react-router-dom";



const SearchCoin= styled(Box)`
background:#fff;
width:20rem;
height:2.2rem;
margin-left:.4rem;

border-radius:.1rem;
display:flex;
align-item:center;
justify-content:center;
`
const NewInputBase = styled(InputBase)`
padding-left:0;
width:100%;
height:100%
`
const SearchIcons = styled(Box)`
padding-top:0.4rem;
paddingRight:.4rem;

`

function Search(){
const [text,setText]=useState('');
    const getText =(e)=>{
        setText(e)
        console.log(text);



    }

    const product = useSelector((state)=> state.Users.product)

    const products = product.filter(product => product.title.toLowerCase().includes(text.toLowerCase()));

    console.log(products)

    return(
        <SearchCoin>
            <NewInputBase style={{fontSize:".9rem", paddingLeft:"1rem"}}  placeholder="Search for products, brands and more" onChange={(e)=>getText(e.target.value)} />
            <SearchIcons style={{color:"#2874f0" , height:"1rem"}}>
                <SearchIcon />
            </SearchIcons>
        {text?
       
        <List style={{display:'block',direction:'column',position:"absolute",marginTop:"3rem",background:"white",color:'black' }}>
       { products.map((data)=>(
            <ListItem key={nanoid()}>
         <NavLink to={"/product/"+data.id} style={{textDecoration:"none"}} onClick={()=>setText('')} >   <Typography>{data.title}</Typography> </NavLink>  
            </ListItem>

            ))   }
        </List>


    
       :null }
        </SearchCoin>
    )
}


export default Search;