
import { Dialog, TextField,Box, Typography,Button } from "@mui/material";
import { useState,useContext } from "react";
import styled from "styled-components";
import {AuthenticateSignUp,AuthenticateLogin} from "./api";
import { DataContext } from "../../context/DataProvider";





const Component = styled(Box)`
display:flex;
height:85vh;
flex-direction:column;
padding:2rem 1rem;
gap:1.3rem;

position:top;
width:60%;
flex:1;
 

`
const LoginButton = styled(Button)`
background:#fb641b;
`

const Image = styled(Box)`
background: #2874f0 url(" https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/login_img_c4a81e.png") center  80% no-repeat; 
width:40%;
`


function LoginDialog({open,setOpen}){

 const [toggle,setToggle] =useState(true);
 const [{ setAccount, setLogined }] = useContext(DataContext);

 const [error,setError] = useState(false);
 const [aleredy,setAleredy] =useState(false);
const handleclose =()=>{
    setOpen(false);
    setToggle(true);
   setError(false);
}
const SignupInitial={
  fname:"",
  lname:"",
  username:"",
  password:"",
  email:"",
  phonenumber:"",
}

const LoginInitial ={
  Lemail:"",
  Lpassword:""
}


const [signUp,setSignup] =useState(SignupInitial);
const [login,setLogin]= useState(LoginInitial);
const setInitial =()=>{
    setToggle(false);
}

const inputChange =(e)=>{
 
  setSignup({...signUp,[e.target.name]:e.target.value})

}

const valueChange =(e)=>{
  setLogin({...login,[e.target.name]:e.target.value})

}
const newOne = async()=>{
  const response = await AuthenticateSignUp(signUp)
  
  if(response==='exist'){
setAleredy(true)
  }else{
    setAccount(signUp.fname);
    setOpen(false);
    setLogined(true);
   
    console.log(signUp.fname,'from signup');
  }
}

const SignupUser = async ()=>{
 const response = await  AuthenticateSignUp(signUp)
 if(response){
 setAccount(signUp.fname);
 setOpen(false);


 console.log(signUp.fname,'from signup');
 }
 else{
  handleclose()
  
}  ;


 
 console.log("hello error");

}



const handleLogin = async ()=>{
  const responseData = await  AuthenticateLogin(login);
  if(responseData){
    const name= responseData.data.fname
    console.log(name,'recived');
    setAccount(name);
    setOpen(false)
   

  }
  

  
  if(!responseData)
    {
setError(true);


    }
  
 
  
 
 
 }



return(
  <Dialog open={open} onClose={handleclose} style={{height:"100vh",width:"90vw",marginLeft:"10vw"}}>
  {
    toggle?
  
  <Box style={{display:"flex" , height:"100%"}}>
  <Image>
  <Typography variant='h5' style={{margin:"2rem 2rem",color:"white",fontWeight:"700"}} >Login</Typography>
  <Typography style={{marginLeft:"2rem", width:"70%",color:"whitesmoke"}}>Get access to your Orders, Wishlist and Recommendations</Typography>

  </Image>
 
  <Component>
 
    <TextField  variant="standard" onChange={(e)=>valueChange(e)} name="Lemail" label="Enter Username"></TextField>
    <TextField variant="standard" onChange={(e)=>valueChange(e)} name="Lpassword" label="Enter Password"></TextField>
   {error? <Typography style={{fontSize:".7rem",color:'red',fontWeight:"600"}}>Please enter valid username or password</Typography>:null}
    <Typography style={{fontSize:".8rem",color:"gray"}}>By continuing, you agree to Flipkart's Terms of Use and Privacy Policy.</Typography>
    <LoginButton style={{background:"#fb641b" ,color:"white",textTransform:"none",height:"3rem"}} onClick={handleLogin}>LOGIN</LoginButton>
    <Typography style={{textAlign:"center"}}>OR</Typography>
    <Button style={{boxShadow:"0 2px 4px 0 rgb(0 0 0/20%) ",textTrasform:"none",color:"#2874f0",height:"3rem"}}>Request OTP</Button>
    <Typography style={{fontSize:"1rem", textAlign:"center" ,color:"#3874f0", cursor:"pointer",fontWeight:"600"}} onClick={setInitial}>New to Flipkart? Create an account</Typography>
  </Component>
  </Box>
  :
  <Box style={{display:"flex" , height:"100%"}}>
  <Image>
  <Typography variant='h5' style={{margin:"2rem 2rem",color:"white",fontWeight:"700"}}>Looks you're new here!</Typography>
  <Typography style={{marginLeft:"2rem", width:"70%",color:"whitesmoke"}}>Sign Up with your mobile number to get started</Typography>

  </Image>
 
 
  <Component>
  
    <TextField  variant="standard" name="fname" label="Enter FirstName" onChange={(e)=>inputChange(e)}></TextField>
    <TextField variant="standard"  name="lname" label="Enter LastName"  onChange={(e)=>inputChange(e)}></TextField>
    <TextField variant="standard" name="username" label="Enter Username"  onChange={(e)=>inputChange(e)}></TextField>
    <TextField variant="standard"  name="email" label="Enter Email"  onChange={(e)=>inputChange(e)}></TextField>
    <TextField variant="standard"  name="password" label="Enter Password"  onChange={(e)=>inputChange(e)}></TextField>

    <TextField variant="standard"  name="phonenumber" label="Enter PhoneNumber"  onChange={(e)=>inputChange(e)}></TextField>
    {aleredy?  <Typography style={{fontSize:'.8rem',color:'red'}}>USER ALEREDY EXIST</Typography>:null}

    <Button style={{background:"#fb641b" ,color:"white",textTransform:"none",height:"4rem"}} onClick={newOne}>Cointinue</Button>
  </Component>
  
  </Box>
  }

  </Dialog>
)


}

export default LoginDialog;