import { Box, Button, Divider, Typography } from "@mui/material";
import Carousel from "react-multi-carousel"
import 'react-multi-carousel/lib/styles.css';
import styled from "styled-components";
import Countdown from 'react-countdown'
import {   NavLink } from "react-router-dom";
import { nanoid } from "@reduxjs/toolkit";
import { useSelector } from "react-redux";
import { useEffect } from "react";
import { fetchProduct } from "../../redux/reducer/Slice";
import { useDispatch } from "react-redux";
import datas from "./images";
 
const responsive = {desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 5
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 3
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 3
  }
};

const Component = styled(Box)`
margin-top:1.2rem;
background:#FFFF;
text-decoration:none;


`

const Deal = styled(Typography)`
fontSize:1.4rem;
font-weight:900;
display:flex;
padding:1rem 1.2rem;
gap:1rem;
`
const CustomNav = styled(NavLink)`
text-decoration:none;
`




 function SmartphoneSlide({title,timer,cate}) {

  




    const timerURL = 'https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/timer_a73398.svg';
    const renderer =({hours,minutes,seconds})=>{
      return <Box variant='span'> {hours}:{minutes}:{seconds} Left</Box>
    }
    const cat = cate;
   
    const products = useSelector((state)=> state.Users.product)
    const product = products.filter(x => x.category===cat)
    const newData = datas.filter(x=> x.type ===cat)
    
       
    

    return(
        <Component key={nanoid()}>
       
          <Box>
    
          <Deal style={{fontSize:"1.2rem",fontWeight:"600",display:"flex",padding:"1rem 1.2rem" ,gap:"1rem"}}> {title}
          { timer?  <Box style={{color:"#7f7f7f", display:"flex"}}>
          
           <img style={{width:'1.2rem',alignItem:"center"}} src={timerURL} alt="time"/>
          <Countdown date={Date.now() + 5.04e+7}  renderer={renderer}/>
        </Box>
        :null}
        <Button style={{marginLeft:"auto",background:"#3874f0",color:"white", borderRadius:".2rem",fontWeight:"600"}} variant="cointained"  >View All</Button>

      </Deal>
      <Divider />



     <Box  display={'flex'}>
     <Box width={'20%'}>
   <img src={newData[0].url} alt="auto" style={{height:"18rem" ,width:'10rem'}} />
 
    
     </Box>
    <Box  width={'80vw'}>
      <Carousel
       
        responsive={responsive}
        swiseable={false}
       draggable={false}
       dotListClass="custom-dot-list-style"
       containerClass="carousel-container"
       centerMode={true}
       autoPlay={true}
       keyBoardControl={true}
       autoPlaySpeed={3000}
       infinite={true} >
        
        {product.map((products) =>(

<Box  key={nanoid()} textAlign={"center"} padding={"1.5rem 1rem"} display={"flex"} flexDirection={"column"}>
<CustomNav exact='true' to={"/product/"+products.id} style={{textDecoration:"none"}}> 

          <img style={{width:"8rem",height:"7rem"}} src={products.images[1]} alt="product"/>
          <Typography style={{color:"#212121",fontSize:".9rem", fontWeight:"600" ,textAlign:"center",padding:"2rem 2rem 0 0"}}>{products.title}</Typography>

</CustomNav>


</Box>
        ))}



        </Carousel>
        </Box>
        </Box>


     
     
    
        </Box>
       
        </Component>
    )
}

export default SmartphoneSlide;