import { AppBar,Toolbar,Box,styled, Typography, IconButton, Drawer,List,ListItem} from "@mui/material";
import Search from "./Search";
import CustomButton from "./CustomButton"
import { NavLink } from "react-router-dom";
import { Margin,Menu } from "@mui/icons-material";
import { useState } from "react";


const StyledHeader = styled(AppBar)`
background-color: #2874f0;
height:3.5rem;

@media (max-width: 426px) {
    /* CSS changes for screens with a maximum width of 768px */
height:4rem; 
display:flex;
justify-content:center;
  }
`;

const Component = styled(Box)`
margin-left:11%;
line-height:0;
`
const SubHeading= styled(Typography)`
font-size:.6rem;
font-style:italic;

`

const MenuButton=styled(IconButton)(({theme})=>({
    display:'none',
    [theme.breakpoints.down('sm')]:{
        display:'block'
    }

}))
const CustomButtonWrapper=styled(Box)(({theme})=>({
    margin:"0 5% 0 auto" ,
    [theme.breakpoints.down('sm')]:{
      display:'none'
    }
}))

function Header(){
    const LogoImage= "https://static-assets-web.flixcart.com/fk-p-linchpin-web/fk-cp-zion/img/flipkart-plus_8d85f4.png"
    const LogoImage2="https://static-assets-web.flixcart.com/fk-p-linchpin-web/fk-cp-zion/img/plus_aef861.png";
    const [open,setOpen] =useState(false);

    const list =()=>(
        <Box style={{display:"grid",gridTemplateColumn:"",width:"13rem"}}>
            <List>
                <ListItem >
                    <CustomButton  />
                </ListItem>
            </List>
        </Box>

    )
    return(
        <div>

        <StyledHeader style={{minHeight:"4rem"}}>
            <Toolbar >
           <MenuButton onClick={()=>{setOpen(true)}} >
            <Menu />
           </MenuButton>
           <Drawer open={open} onClose={()=>{setOpen(false)}}>
                        {list()}
           </Drawer>
           <Box style={{display:'flex',flexDirection:'right'}}>
            <Component>
            
            <NavLink exact='true' to={"/"} style={{textDecoration:"none", color:"inherit"}}>
            <img  src={LogoImage} alt="Logo"  style={{width:"5rem",paddingLeft:"3rem", height:"1.5rem"}}/> 

            <Box style={{display:"flex", paddingLeft:"3rem"}}>
                <SubHeading >
                Explore
               <Box component="span" style={{color:"#FFE500"}}> Plus </Box>  
                  <img  src={LogoImage2} alt="Logo2" style={{width:".6rem"}}/>

                </SubHeading>
            </Box>
          
            </NavLink>
            </Component>
           
            <Search />
           </Box>
            <CustomButtonWrapper>
                <CustomButton />
            </CustomButtonWrapper>

            </Toolbar>
        </StyledHeader>

        </div>
    )
}

export default Header;