
import styled from "styled-components";
import { navData } from "./NavbarData";
import { Box, Typography } from "@mui/material";


const Components = styled(Box)`
display:flex;
gap:2.4rem;
margin-left:0;
justify-content:center;
border:0 0 .3rem 0 solid ;


`
const Texted = styled(Typography)`
font-size:.10px;

`

function Navbar(){
    return(
        <Components style={{fontWeight:"600"}}>
            {navData.map(data=>(

         <Box style={{display:"flex" ,flexDirection:"column"}} key={data.url}>
            <img src={data.url} alt="nav" style={{height:"4.1rem",width:"4rem",display:"flex" ,flexDirection:"row",marginTop:".5rem"}}/>
            <Texted style={{fontSize:".8rem",alignText:"center",fontWeight:"600",paddingLeft:".4rem",fontFamily:"inherit"}}>{data.text}</Texted>
         </Box>


            ))}
        </Components>

    )
}


export default Navbar;